@font-face {
	font-family: "iconfont";
	src:url("../fonts/iconfont.eot?ac2315");
	src:url("../fonts/iconfont.eot?ac2315") format("embedded-opentype"),
	url("../fonts/iconfont.ttf?ac2315") format("truetype"),
	url("../fonts/iconfont.woff?ac2315") format("woff");
	font-weight: normal;
	font-style: normal;
}

.icon-iconfont() {
	font-family: "iconfont";
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	font-size: inherit;
	text-transform: none;
	text-rendering: auto;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	transform: translate(0, 0);
}


